<template>
    <el-container>
        <el-header>
            <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-news">保存</el-button>
            <el-button type="info" @click="back()" icon="el-icon-back">返回</el-button>
        </el-header>
        <el-main>

            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
                <el-form-item label="用户组名称" prop="name">
                    <el-input placeholder="请输入用户组名称"
                              v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="菜单权限" prop="">
                    <el-tree
                            :data="treeData"
                            show-checkbox
                            :props="defaultProps"
                            node-key="id"
                            highlight-current
                            ref="tree"
                            default-expand-all>
                    </el-tree>
                </el-form-item>

            </el-form>

        </el-main>

    </el-container>
</template>
<script>

    export default {
        data() {
            return {
                ruleForm: {
                    id: '',
                    name: '',
                    menus: [],
                    halfHenus: [],
                },
                treeData: [],
                defaultProps:
                    {
                        children: 'child',
                        label: 'alias'
                    },
                rules: {
                    name: [
                        {
                            required: true,
                            message: '请输入用户组名称',
                            trigger: 'blur'
                        },
                    ],

                }
            }
        },
        mounted() {
            this.axios.get('/api/menu/get').then((response) => {
                this.treeData = response.data.data;
            }).catch((response) => {
                console.log(response);
            })
            if (this.$route.query.id != undefined) {
                this.ruleForm.id = this.$route.query.id;
                this.ruleForm.name = this.$route.query.name;
                this.axios.get('/api/userGroup/get/' + this.$route.query.id).then((response) => {
                    console.log(response.data.data)
                    var v = [];
                    for (var index in response.data.data) {
                        v.push(response.data.data[index].id)
                    }
                    this.$refs.tree.setCheckedKeys(v);
                }).catch((response) => {
                    console.log(response);
                })
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {

                        if (this.$refs.tree.getCheckedKeys().length == 0) {
                            this.$message({
                                type: 'info',
                                message: '请选择菜单'
                            });
                            return;
                        }
                        this.ruleForm.menus = this.$refs.tree.getCheckedKeys();
                        this.ruleForm.halfHenus = this.$refs.tree.getHalfCheckedKeys();

                        this.axios.post('/api/userGroup/save', {
                            ...this.ruleForm
                        }).then((response) => {
                            if (response.data.state == '0') {
                                this.back();
                            } else {
                                this.$message({
                                    type: 'info',
                                    message: '保存失败'
                                });
                            }
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            back() {
                this.$router.push({path: '/userGroupManage/index', replace: true});
            }
        }
    }
</script>

<style>
    .demo-ruleForm {
        width: 100%;
    }

    .el-form-item__content {
        line-height: 20px;
    }
</style>
